@import "root";
@import "var";
@import "node_modules/bootstrap/scss/functions";
@import "node_modules/bootstrap/scss/variables";
@import "node_modules/bootstrap/scss/mixins";
@import "node_modules/bootstrap/scss/bootstrap-grid";
@import "node_modules/bootstrap/scss/transitions";
@import "node_modules/bootstrap/scss/nav";

@import "typography";
@import "mixin";

@import "node_modules/@pqina/flip/dist/flip";

//@import "preloader"

body {
  position: relative;
  min-width: 320px;
  font-family: $text;
  font-weight: 400;
  background-color: $clear;
  color: $gray;
  font-style: normal;
  font-size: 14px;
  overflow-x: hidden;
  margin: 0;

  &.overflow {
    overflow: hidden;
    width: 100%;
    height: 100%; }

  #page {
    display: flex;
    min-height: 100vh;
    flex-direction: column;

    main {
      flex: 1; } } }

.site-header {
  padding-top: 15px;
  padding-bottom: 15px;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 1000;
  @include media-breakpoint-down(sm) {
    padding-top: 19px; }

  > .container-fluid {
    max-width: 1750px;
    margin-left: auto;
    margin-right: auto; }
  .zi-i {
    z-index: 1;
    @include media-breakpoint-down(lg) {
      z-index: 0; } }

  .header-logo {
    display: inline-block;
    .dark-logo {
      width: 100%;
      max-width: 260px;
      max-height: 60px;
      @media (max-width: 1400px) {
        // width: 230px
 }        // height: 55px
      @media (max-width: 1300px) {
        // width: 220px
 }        // height: 50px
      @include media-breakpoint-down(lg) {
        width: 160px;
        height: 30px; } } }

  .search-form {
    display: none;
    position: relative;
    padding: 0 15px;
    margin-top: 20px;

    #s { // input
      width: 100%;
      border: none;
      border-bottom: 1px solid $lite-gray;
      line-height: 58px;
      font-size: 16px; }

    .btn-search {
      position: absolute;
      right: 9px;
      top: 20px;
      border: none;
      background-color: transparent; } }

  .search-form.show {
    display: none;
    position: fixed;
    background: transparent;
    width: 28.4%;
    margin: 0;
    padding: 0;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -87%);
    @include media-breakpoint-down(md) {
      width: 50%; }
    @include media-breakpoint-down(sm) {
      width: 80%;
      transform: translate(-50%, -50%); }
    #s {
      background: transparent;
      caret-color: $clear;
      color: $clear;
      font-family: $text; }
    #s:-webkit-autofill {
      -webkit-text-fill-color: $clear; }
    #s:-webkit-autofill,
    #s:-webkit-autofill:hover,
    #s:-webkit-autofill:focus,
    #s:-webkit-autofill:active {
      -webkit-box-shadow: 0 0 0 30px transparentize($dark-gray, .1) inset;
      padding: 0 10px;
      &::placeholder {
        color: $clear;
        transition: .3s; }
      &:hover {
        &::placeholder {
          color: darken($clear, 20); } }
      &:focus {
        &::placeholder {
          color: transparent; } } }
    .btn-search {
      right: -6px;
      top: 18px;
      opacity: 0.8;
      transition: .3s;
      &:hover {
        opacity: 1; }
      .icon-search {
        background-image: url('../img/icons/white-search.svg'); } } }

  .bg-show-search {
    display: none;
    .icon-close {
      display: none; } }
  .bg-show-search.show {
    display: none;
    background: $opacity-gray;
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: 0;
    .icon-close {
      display: block;
      cursor: pointer;
      border: 1px solid $clear;
      border-radius: 50%;
      text-align: center;
      line-height: 26px;
      font-family: $title;
      font-size: 30px;
      width: 30px;
      height: 30px;
      top: 20%;
      right: 21%;
      color: $clear;
      background: transparent;
      position: fixed;
      @include media-breakpoint-down(sm) {
        right: 15px;
        top: 15px; } } }

  .header-nav {
    @include media-breakpoint-down(lg) {
      padding-bottom: 50px;
      position: fixed;
      right: -110%;
      top: 0;
      min-width: 360px;
      max-width: 600px;
      background-color: $clear;
      box-shadow: 0 0 45px rgba($dark, .1);
      @include transition(.3s ease);
      &.header-nav-open {
        right: 0;
        @include transition(.3s ease); } }
    @include media-breakpoint-down(lg) {
      min-width: 95%; }
    @include media-breakpoint-up(xl) {
      display: grid;
      grid-template-columns: 1fr 310px; }
    @media screen and (max-width: 1380px) {
      grid-template-columns: 1fr 250px; }
    .menu {
      padding-left: 30px;
      margin-bottom: 0;
      display: inline-flex;
      justify-content: center;
      @media (max-width: 1500px) {
        padding-left: 0px; }
      @include media-breakpoint-down(lg) {
        padding-top: 50px;
        display: block; }
      @include media-breakpoint-up(xl) {
        &#menu-header, #menu-header-login {
 } }          // justify-self: center

      .menu-item {
        list-style-type: none;
        display: inline-block;
        @include media-breakpoint-down(lg) {
          display: block;
          padding: 5px; }
        a {
          font-weight: 500;
          font-size: 16px;
          line-height: 26px;
          text-transform: uppercase;
          color: $dark-gray;
          text-decoration: none;
          margin: 27px 15px;
          display: block;
          transition: .3s;
          &:hover {
            color: $gold;
            text-underline-position: below; }
          &:active,
          &:focus {
            border-bottom: 2px solid $gold; }
          @media screen and (max-width: 1439px) {
            margin: 25px 10px;
            font-size: 15px; }
          @media screen and (max-width: 1380px) {
              // margin: 25px 6px
              font-size: 14px; }
          @media screen and (max-width: 1250px) {
            font-size: 14px; }
          @include media-breakpoint-down(lg) {
            margin: 10px;
            &:hover {
              border: none; } } } }
      .sign-up,
      .log-in,
      .nav-search {
        @include media-breakpoint-down(lg) {
          width: 100%; }
        .icon {
          margin-bottom: -5px; }
        a {
          @media screen and (max-width: 1430px) {
            font-size: 0; }
          @media screen and (max-width: 1199px) {
            font-size: 14px; }
          &:hover {
            border: none; } } }
