      .favorites,
      .message-history,
      .personal-data {
        // display: inline-block
        @include media-breakpoint-down(lg) {
 }          // display: inline-block
        a {
          display: inline-block;
          border-radius: 50%;
          width: 36px;
          height: 36px;
          margin: 0 10px 0;
          transform: translateY(-10px);
          background: $dark-gray;
          font-size: 0;
          border-bottom: none;
          background-repeat: no-repeat;
          background-position: center center;
          &:hover {
