      // search
      .nav-search {
        display: inline-block;
        margin-left: 20px;
        transform: translateY(10px);
        @include media-breakpoint-down(lg) {
          margin-left: 10px; }
        .icon {
          margin-bottom: 0; }

        a {
          padding-right: 0;
          &:hover {
            border: none; }
          .icon-search {
            position: relative;
            top: 5px; } } } } }

  .open-nav {
    .open-nav-icon {
      cursor: pointer;
      width: 30px;
      height: 20px;
      fill: $dark-gray; } }

  .close-nav {
    cursor: pointer;
    position: absolute;
    right: 15px;
    top: 24px; } }

.sticky-add {
  @include transition(.5s); }

.sticky-bg {
  @include transition(.5s);
  background-color: $clear;
  box-shadow: 0 0 40px rgba($gray, .2); }

.site-header {
  .header-nav {
    .menu.menu-area {
      display: inline-flex;
      justify-content: flex-end;
      padding-top: 15px;
      padding-left: 0;
      @include media-breakpoint-down(lg) {
        padding-top: 15px; } }
    #menu-header-no-login-buttons {
      padding-left: 0;
      @include media-breakpoint-down(lg) {
        padding-top: 0px; }
      .nav-search {
        a {
          margin: 15px;
          @media screen and (max-width: 1439px) {
            margin: 12px 15px; }
          @media screen and (max-width: 1430px) {
            margin: 10px 15px; } } } } } }


