  background-color: #FECB62;
  color: #39373C; }

#counter .tick-flip * {
  -webkit-border-radius: 0;
  -moz-border-radius: 0;
  border-radius: 0; }

#counter .tick-flip-panel-back, #counter .tick-flip-panel-front {
  height: 50%;
  box-shadow: unset; }

#counter .tick-flip {
  width: 70px;
  min-width: unset;
  border: 3px solid #1F1F1F;
  text-indent: 1px;
  letter-spacing: 0;
  margin-left: 3px;
  margin-right: 3px;
  border-radius: 0;
  position: relative; }
  #counter .tick-flip:before {
    position: absolute;
    content: '';
    height: 2px;
    width: calc(100% - 10px);
    background-color: #1F1F1F;
    left: 5px;
    top: calc(50% - 1px);
    z-index: 22; }

#counter .tick-flip-card:before, #counter .tick-flip-card:after {
  z-index: 11;
  content: '';
  position: absolute;
  width: 7px;
  height: 22px;
  background: #575143;
  border: 2px solid #1F1F1F; }

#counter .tick-flip-card:before {
  left: -2px;
  top: calc(50% - 11px); }

#counter .tick-flip-card:after {
  right: -2px;
  top: calc(50% - 11px); }

#counter .tick-flip-panel-back:after {
  background-image: unset; }

